@import "color";
body {
    background-color: #F8FAFC;
}
.inner-top {
    .tab {padding: 1rem; border-top-left-radius: .75rem;  border-top-right-radius: .75rem; width: 120px; text-align: center; cursor: pointer;
        &.active {background-color: $primary; color: $white;}
    }
}
.inner-main {
    .tab { padding: 1rem; width: 120px; text-align: center; cursor: pointer; background-color: #fff; border-radius: .375rem;
        &:not(:first-child) {margin-left: 1rem;}
        &.active {
            background-color: $primary;
            color: #fff;
        }
        &-list {display: flex;}
    }
}
.view-btn {width: 100%; background-color: $primary; color: #fff; font-size: 1.2rem;}
.detail-btn {width: 100%; background-color: $info; color: #fff; font-size: 1.2rem;}

.lecture {
    &-wrap {width: 100%;border-radius: .375rem; border: 1px solid #e5e7eb;}
    &-list {margin: 0 -0.5rem;}
    &-item {width: 25%; padding: .5rem .5rem;
    }
    &-tlt {font-size: 1.2rem; font-weight: 700; margin-bottom: .5rem;}
}

.progress {
    &-bar {height: 6px; border-radius: 9999px; background-color: #e5e7eb; overflow: hidden;
        .progress-fill {height: 100%; background: linear-gradient(90deg, #2563eb, #3b82f6);}
    }
}

@media screen and (max-width: 768px) {
    .inner {padding: .5rem;}
    .tlt-wrap {padding: 0 .5rem;}
    .inner-main {
        .tab-wrap {width: 100vw; overflow: scroll;}
        .lecture-list {margin: 0;}
        .lecture-item {width: 50%;}
    }
}
